    max-width: 60%;\n\n            @media screen and (max-width: 640px) {\n                max-width: 50%;\n            }\n        }\n\n    }\n\n    .icon-profile {\n        display: flex;\n        justify-content: center;\n        border-radius: 300px;\n        max-height: 200px;\n    }\n\n    .reseaux {\n\n        @extend .row;\n        padding-top: 1rem;\n        justify-content: center;\n        align-items: center;\n\n    }\n}\n\n//card masonry\n.card-masonry {\n    margin: 1vw 0.5vw;\n    transform: scale(1);\n    transition: ease-in-out transform .5s;\n\n    img {\n\n        border-radius: 10px;\n        width: 100%;\n        transform: scale(1.0);\n        transition: ease-in-out transform .5s;\n        object-fit: fill;\n\n        &:hover {\n            transform: scale(1.05);\n            transition: ease-in-out transform .5s;\n        }\n\n    }\n\n\n\n}\n\n.experiences {\n    background-color: var(--color-contrast);\n\n    @media screen and (min-width: 980px) {\n        &.after-left {\n            position: relative;\n\n            &::after {\n                position: absolute;\n                top: 27px;\n                right: -14px;\n                display: inline-block;\n                border-top: 14px solid transparent;\n                border-left: 14px solid var(--color-contrast);\n                border-right: 0 solid var(--color-contrast);\n                border-bottom: 14px solid transparent;\n\n                content: \" \";\n            }\n        }\n\n        &.after-right {\n            position: relative;\n\n            &::after {\n                position: absolute;\n                bottom: 20px;\n                left: -14px;\n                display: inline-block;\n                border-top: 14px solid transparent;\n                border-right: 14px solid var(--color-contrast);\n                border-left: 0 solid var(--color-contrast);\n                border-bottom: 14px solid transparent;\n\n                content: \" \";\n  